export class Article {
    id: number;
    imgSrc: string;
    title: string;
    by: string;
    byDescription: string;
    byAvatarSrc: string;
    date: string;
    text: string;
    tags: string[];
    commentsNumber: number;

    constructor(
        id: number,
        imgSrc: string,
        title: string,
        by: string,
        byDescription: string,
        byAvatarSrc: string,
        date: string,
        text: string,
        tags: string[],
        commentsNumber: number
    ) {
        this.id = id;
        this.imgSrc = imgSrc;
        this.title = title;
        this.by = by;
        this.byDescription = byDescription;
        this.byAvatarSrc = byAvatarSrc;
        this.date = date;
        this.text = text;
        this.tags = tags;
        this.commentsNumber = commentsNumber;
    }
}
